"use client"

import { usePathname } from 'next/navigation'
import { 
  Breadcrumb, 
  BreadcrumbList, 
  BreadcrumbItem, 
  BreadcrumbLink, 
  BreadcrumbPage, 
  BreadcrumbSeparator 
} from '@/components/ui/breadcrumb'
import React from 'react'

export default function CommonBreadcrumb() {
  const pathname = usePathname()
  
  // 현재 경로에 따른 breadcrumb 생성
  const getBreadcrumbs = () => {
    const segments = pathname.split('/').filter(Boolean)
    const breadcrumbs = []
    
    // 홈
    breadcrumbs.push({
      label: 'Home',
      href: '/',
      isActive: false
    })
    
    // 책무구조 Master
    if (segments.includes('master')) {
      breadcrumbs.push({
        label: '책무구조 Master',
        href: '/master',
        isActive: false
      })
      
      // executive_front (임원관리 Master)
      if (segments.includes('executive_front')) {
        breadcrumbs.push({
          label: '임원관리 Master',
          href: '/master/executive_front',
          isActive: true
        })
      }
      
      // organization (직책(조직) Master)
      if (segments.includes('organization')) {
        breadcrumbs.push({
          label: '직책(조직) Master',
          href: '/master/organization',
          isActive: true
        })
      }
      
      // meeting (회의체 Master)
      if (segments.includes('meeting')) {
        breadcrumbs.push({
          label: '회의체 Master',
          href: '/master/meeting',
          isActive: true
        })
      }
      
      // department (책무관리 Master)
      if (segments.includes('department')) {
        breadcrumbs.push({
          label: '책무관리 Master',
          href: '/master/department',
          isActive: true
        })
      }
    }
    
    // 책무 관리
    if (segments.includes('responsibility-management')) {
      breadcrumbs.push({
        label: '책무 관리',
        href: '/responsibility-management',
        isActive: false
      })
      
      // allocation (책무 배분)
      if (segments.includes('allocation')) {
        breadcrumbs.push({
          label: '책무 배분',
          href: '/responsibility-management/allocation',
          isActive: true
        })
      }
    }
    
    return breadcrumbs
  }
  
  const breadcrumbs = getBreadcrumbs()

  return (
    <div className="w-full">
      <Breadcrumb>
        <BreadcrumbList className="text-base font-medium">
          {breadcrumbs.map((breadcrumb, index) => (
            <React.Fragment key={index}>
              <BreadcrumbItem>
                {breadcrumb.isActive ? (
                  <BreadcrumbPage>{breadcrumb.label}</BreadcrumbPage>
                ) : (
                  <BreadcrumbLink href={breadcrumb.href}>
                    {breadcrumb.label}
                  </BreadcrumbLink>
                )}
              </BreadcrumbItem>
              {index < breadcrumbs.length - 1 && <BreadcrumbSeparator>/</BreadcrumbSeparator>}
            </React.Fragment>
          ))}
        </BreadcrumbList>
      </Breadcrumb>
    </div>
  )
}
